.heading{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    font-weight: 900;
   color: white;
    font-family:'Times New Roman', Times, serif;
    /* background-image: linear-gradient(180deg,#00C9FF 0%,#92FE9D 100%); */
}

.box{
    min-height: 100vh;
    
    background-image: url("https://images.pexels.com/photos/209831/pexels-photo-209831.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.container{
    width: 580px;
    height: 580px;
    margin: auto;
    margin-top: 5px;
    border-radius: 12px;
    /* background-image: linear-gradient(180deg,#130754 0%,#3b2f80 100%); */
    background-color: rgba(128, 128, 128, 0.451);
}

.top-bar{
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 60px;

}

.top-bar input{
    display: flex;
    width: 360px;
    height: 55px;
    background: #ebfffc;
    outline: none;
    border-radius: 40px;
    padding-left: 37px;
    color: #626262;
    font-size: 20px;
    font-weight: 400;
}

.search-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 65px;
    height: 57px;
    background: #ebfffc;
    border-radius: 80px;
    cursor: pointer;
}

.search-icon:hover{

    cursor: pointer;
   transform: scale(1.1);
   transition-delay: 0.3s;
   translate: 100px 20px;
   transition-duration: 0.5s;
}

.weather-image{
    margin-top: 7px;
    display: flex;
    justify-content: center;
    
}

.weather-image:hover{
    transform: scale(1.1);
    transition-delay: 0.3s;
    translate: 100px 20px;
    transition-duration: 0.5s;
}

.weather-temp{
    display: flex;
    justify-content: center;
    color: black;
    font-size:40px;
    font-weight: 600;

}

.weather-location{
    display: flex;
    justify-content: center;
    color: black;
    font-size: 35px;
    font-weight: 600;

}

.data-container{
    margin-top: 50px;
    color: black;
    display: flex;
    justify-content: center;

}

.element{
    margin:auto;
    display: flex;
    align-items: flex-start;
    gap: 12px;

}

.data{
    font-size: 25px;
    font-weight: 700;

}

.text{
    font-size: 20px;
    font-weight: 700;
}

.icon{
    margin-top: 10px;

}